import React, { Fragment } from 'react'

const Tuto = () => {
    return (
        <Fragment>
    <div class="title">赛制规则</div>
        
              <h2 style={{ width: '800px', margin: '150px', color: 'black' }}>&nbsp;&nbsp; (一) 组织与报名方式：

1. 本次大赛统一按照在线注册报名、线上初赛、线下决赛三个阶段组织各阶段比赛。

2. 本次大赛采用双赛道机制，分别为全国赛道和校内赛道。

3. 双赛道均采取单人挑战的形式，河北师范大学的全日制在校大学生（包括本科生、研究生）可参加校内赛道，全国其他具有正规学籍的全日制在校大学生（包括本科生、研究生）须参加全国赛道。

4. 每位选手仅限参加一个赛道进行比赛。



具体赛事信息：

http://hectf.hsca.org.cn/

若官网无法访问请先进入官网QQ群咨询相关信息。</h2>    

<h2 style={{ width: '800px', margin: '150px', color: 'black' }}>
(二) 参赛对象及赛制说明：

全国具有正规学籍的全日制在校大学生（包括专科生、本科生、研究生），尤其推荐全国各学校的2019级或2020级学生参赛。

 本次大赛的线上初赛使用CTF-JEO（解题）赛制，选手通过解决预设题目(赛题方向包括但不限于Web应用安全，软件逆向，二进制漏洞挖掘，密码学，安全杂项)获得相应题目的Flag（旗标文件），回答正确可以获得相应的分值。比赛结束后，按照分数排名，分数相同的按时间排名。

线下决赛使用的赛制将在初赛结束后另行通知。</h2>
<h2 style={{ width: '800px', margin: '150px', color: 'black' }}>
(三)
1. 线上初赛-全国赛道的前六名以及线上初赛-校内赛道前六名的选手将拥有晋级资格。

2. 线上初赛-全国赛道的前六名拥有的晋级资格视为选手所在院校拥有了晋级资格，每所院校仅限晋级一支赛队，前六名中若有多名队员隶属于同一院校则名额顺延，至多顺延至前十八名。

3. 拥有晋级资格的院校可以选出三名校内的2019级及2020级具有正规学籍的全日制在校大学生组成赛队晋级决赛，研究生不允许参与组队。

4. 线上初赛-校内赛道前六名可自由组合成两支队伍晋级决赛。

5. 所有晋级赛队最多有三名队员出战，允许设置一名领队，领队不得以任何形式上场参赛或对场上队员进行影响，不限制领队的年级或身份。

</h2>
           
</Fragment>


        
    )
}

export default Tuto
