import React from 'react'
import { NavLink } from 'react-router-dom'
import './Header.css'

const Header = () => {
    return (
        <header>
            <nav>
                <NavLink activeClassName='active' exact to='/first'><div class="bg">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;首页&nbsp;&nbsp;&nbsp;</div></NavLink>
                <NavLink activeClassName='active' exact to='/home'><div class="bg">&nbsp;&nbsp;大赛概况</div></NavLink>
                <NavLink activeClassName='active' to='/doc'><div class="bg">&nbsp;&nbsp;日程安排</div></NavLink>
                <NavLink activeClassName='active' to='/tuto'><div class="bg">&nbsp;&nbsp;赛制规则</div></NavLink>
                <NavLink activeClassName='active' to='/blog'><div class="bg">&nbsp;&nbsp;奖项设置</div></NavLink>
                <NavLink activeClassName='active' to='/comm'><div class="bg">&nbsp;&nbsp;赛事咨询</div></NavLink>
            </nav>
        </header>
    )
}

export default Header
