import Home from './Home';
import Doc from './Doc';
import Tuto from './Tuto';
import Blog from './Blog';
import Comm from './Comm';
import First from './First';


const routes = [
    {
        path: '/first',
        exact:true,
        component: First
    },
    {
        path: '/home',
        component: Home
    },
    {
        path: '/doc',
        component: Doc,
        

    },
    
    {
        path: '/tuto',
        component: Tuto,
        

    },
    {
        path: '/blog',
        component: Blog
    },
    {
        path: '/comm',
        component: Comm
    },


]
export default routes;